@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
}

/* App */
.App {
  display: flex;
  width: 100vw;
  height: 100vh;
  overflow: hidden;  /* dont need scrollbars */
}

/* Sidebar */
.sidebar {
  width: 300px;
  min-width: 210px;
  height: 100%;
}

/* Main */
.main {
  width: 100%;
  height: 100%;
  background-color: #003fe0;
  background-image: url("https://images.pexels.com/photos/370717/pexels-photo-370717.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

/* User */
.user {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 1rem;
  border: 1px solid #ebebeb;
}

.user .logo img {
  width: 50px;
  border-radius: 50%;
  border: 1px solid #000;
}

.user .info p {
  font-size: 1.1rem;
  font-weight: bold;
}

.user .info a {
  text-decoration: none;
  display: flex;
  justify-content: flex-end;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  z-index: 99;
}
.modal .container {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -25%);
  background-color: white;
}

/* AddNewTodo */
.addnewtodo {
  border-bottom: 1px solid #ebebeb;
}
.addnewtodo .btn {
  padding: 1rem;
  width: 100%;
  height: 70px;
}
.addnewtodo .btn button {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 3px;
  font-size: 1rem;
  background-color: #0080ff;
  color: white;
  font-weight: bold;
  cursor: pointer;
}
.addnewtodo .btn button:hover {
  filter: brightness(1.1);
  transition: all 300ms ease;
}

/* AddNewTodo form */
.todoform {
  padding: 1rem 1rem calc(1rem + 40px) 1rem;
  width: 100%;
  background: white;
  border-radius: 4px;
}
.todoform form .text input {
  padding: 1rem;
  width: 100%;
  margin-bottom: 0.5rem;
  border: none;
  font-size: 1.2rem;
}
.todoform .text input:focus {
  outline: none;
  border-bottom: 1px solid #0080ff;
}
.todoform .remind {
  display: flex;
  align-items: center;
  padding: 0.5rem;
}
.todoform p {
  margin-left: 10px;
}
.todoform .title {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}
.todoform .pick-day {
  padding: 1.5rem 1.5rem 0 1.5rem;
}
.todoform .pick-time {
  padding: 1.5rem;
}
.todoform .pick-project {
  padding: 0.5rem;
}
.todoform .pick-project .projects {
  display: flex;
  flex-wrap: wrap;
  padding: 1rem 1rem 0 1rem;
}
.todoform .pick-project .projects .project {
  border: 1px solid #0080ff;
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
}
.todoform .pick-project .projects .active {
  background: #0080ff;
  color: white;
}
.todoform .cancel {
  position: absolute;
  top: 0;
  right: 5px;
  cursor: pointer;
}
.todoform .confirm {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.todoform .confirm button {
  width: 100%;
  height: 40px;
  background: #0080ff;
  color: white;
  font-size: 1.1rem;
  border: none;
  border-radius: 0 0 4px 4px;
  cursor: pointer;
}

/* Calendar */
.calendar {
  padding: 1rem;
  border-bottom: 1px solid #ebebeb;
}
.calendar .header {
  display: flex;
  align-items: center;
}
.calendar .header .title {
  display: flex;
  align-items: center;
  flex: 1;
}
.calendar .header .title p {
  margin-left: 5px;
  font-size: 1.1rem;
  font-weight: bold;
}
.calendar .header .btns span {
  border-radius: 50%;
  cursor: pointer;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.calendar .header .btns span:hover {
  background-color: #ebebeb;
  transition: background 300ms ease-in-out;
}
.calendar .items {
  padding: 0.5rem;
}
.calendar .items .item {
  padding: 0.25rem;
  margin: 5px;
  font-size: 1rem;
  cursor: pointer;
}

/* Projects */
.projects {
  padding: 1rem;
  border-bottom: 1px solid #ebebeb;
}
.projects .header {
  display: flex;
  align-items: center;
}
.projects .header .title {
  display: flex;
  align-items: center;
  flex: 1;
}
.projects .header .title p {
  margin-left: 5px;
  font-size: 1.1rem;
  font-weight: bold;
}
.projects .header .btns {
  display: flex;
  align-items: center;
}
.projects .header .btns span.edit {
  margin-right: 5px;
}
.projects .header .btns span {
  border-radius: 50%;
  cursor: pointer;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.projects .header .btns span:hover {
  background-color: #ebebeb;
  transition: background 300ms ease-in-out;
}
.projects .items {
  padding: 0.5rem;
}

/* Project */
.project {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0.25rem;
  margin: 5px;
  font-size: 1rem;
}
.project .name {
  cursor: pointer;
  word-break: break-all;
}
.project .btns {
  display: flex;
  align-items: center;
}
.project .btns .total-todos {
  position: absolute;
  right: 0;
  background: #ebebeb;
  width: 25px;
  height: 25px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.project .btns .edit-delete {
  position: absolute;
  right: 0;
  display: flex;
}
.project .btns .edit-delete span {
  border-radius: 50%;
  cursor: pointer;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 300ms ease-in-out;
}
.project .btns .edit-delete span.edit:hover {
  background: #acffac;
}
.project .btns .edit-delete span.delete:hover {
  background: #ffacac;
}

/* AddNewProject */
.addNewProject .add-button span {
  margin-right: 5px;
}
/* projectForm */
.projectForm {
  background-color: white;
  padding: 1rem;
  border-radius: 4px;
  position: relative;
  padding-bottom: 40px;
}
.projectForm input {
  width: 100%;
  height: 40px;
  margin: 15px 0;
  border: none;
  font-size: 1.2rem;
}
.projectForm input:focus {
  outline: none;
  border-bottom: 1px solid #0080ff;
}
.projectForm button {
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 50%;
  border: none;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: filter 300ms ease-in-out;
}
.projectForm button.cancel {
  left: 0;
  background-color: #bebebe;
}
.projectForm button.confirm {
  right: 0;
  background-color: #0080ff;
}
.projectForm button:hover {
  filter: brightness(1.1);
}

/* Todos */
.todos {
  width: 450px;
  height: 80%;
  background-color: white;

  position: absolute;
  bottom: 40px;
  left: 40px;

  border-radius: 4px;
  opacity: 0.9;

  padding: 0 0.5rem 0.5rem 0.5rem;

  overflow-y: auto;
}
.todos .selected-project {
  margin: 1rem 0;
  text-transform: capitalize;
  font-size: 1.2rem;
  font-weight: bold;
  padding-bottom: 2px;
  border-bottom: 1px solid #eeeeee;
}
.todos .to_dos {
  overflow-y: auto;
}

/* Todo */
.todo {
  padding: 0.5rem 1rem;
}
.todo .todo-container {
  display: flex;
  align-items: center;
}
.todo .todo-container .check-todo {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 15px;
}
.todo .todo-container span {
  cursor: pointer;
}
.todo .todo-container .text {
  position: relative;
  flex: 1;
  margin: 0 10px;
  cursor: pointer;
}
.todo .todo-container .text span {
  font-size: 0.8rem;
  color: #bebebe;
}
.todo .todo-container .text .line {
  height: 1px;
  background: #bebebe;
  position: absolute;
  top: 25%;
  width: 0;
  transition: width 250ms ease-in-out;
}
.todo .todo-container .text .line-through {
  width: 100%;
}
.todo .todo-container .add-to-next-day {
  margin-right: 10px;
}

/* EditTodo */
.editTodo {
  position: absolute;
  bottom: 40px;
  left: calc(40px + 450px + 40px);

  background-color: white;

  height: 80%;
  width: 450px;

  border-radius: 4px;
  opacity: 0.9;
}
.editTodo .header {
  margin: 1rem 0.5rem;

  font-size: 1.2rem;
  font-weight: bold;

  padding-bottom: 2px;
  border-bottom: 1px solid #eeeeee;
}

/* Next7Days */
.next7Days .day {
  margin: 5px;
  display: flex;
}
.next7Days .day .name {
  font-weight: bold;
  font-size: 1rem;
}
.next7Days .day .total-todos {
  margin-left: 10px;
}

/* SignIn */
.signIn{
  margin-top: 40vh;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.message{
  background-color: rgb(20, 20, 20);
  color: rgb(246, 234, 210);
  justify-content: center;
  align-items: center;
  padding: 40px;
  border-radius: 12px;
}

button{
  /* background-color: rgb(20, 20, 20); */
  /* color: rgb(246, 242, 234); */
  border-radius: 10px;
  padding: 5px;
  margin-right: 5px;
  font-size: medium;
}